<template>
  <div class="add">
    <el-form  :model="formData" label-width="80px">
      <el-form-item label="新闻标题">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="新闻描述">
        <el-input v-model="formData.desc"></el-input>
      </el-form-item>
      <el-form-item label="新闻类型">
        <el-select v-model="formData.type" placeholder="请选择类型">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="新闻图片">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z1.qiniup.com"
          :show-file-list="false"
          :on-success="upload"
          :data="{token:token}"
        >
          <img v-if="formData.img" :src="formData.img" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="新闻内容">
        <editor v-model="formData.content" :isChange="editorInit"></editor>  <!--局部注册的组件在这里使用-->
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit">
          添加
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import editor from '../../components/wangeditor.vue'
  export default{
    name: "",
    components:{
      editor
    },
    data(){
      return{
        formData:{
          title:'',
          type:'',
          img:'',
          desc:'',
          content:''
        },
        editorInit: false,
        token:'',
        options: [],
      }
    },
    methods:{
      getType(){
        this.$axios.get('getNewsCate').then( res => {
            this.options = res.data
//          console.log(this.options)
        })
      },
      getToken(){
        this.$axios.qiniuGet().then(res => {
          this.token = res.data
        })
      },
      upload(res){
        this.formData.img = res.url
      },
      submit(){
        this.$nextTick(() => {
          this.editorInit = true;
        });
        this.$axios.post('addNews',this.formData).then( res => {
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: '添加成功!'
            });
          }else{
            this.$message({
              type: 'error',
              message: '添加失败!'
            });
          }
          setTimeout(() => {
            this.$router.push('/news')
          },1000)
        }).catch( () => {
          this.$message({
            type: 'warning',
            message: '已取消删除'
          });
        })
      }
    },
    created(){
      this.getType();
      this.getToken();
    }
  }
</script>
<style scope>
  .add{
    width: 100%;
    height:100%;
    margin-right: 22px;
    padding-left:11px;
    padding-top: 20px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



